.button {
  border: 1px solid;
  background-color: transparent;
  padding: 6px 20px;
  text-transform: uppercase;
  letter-spacing: 2px;
  cursor: pointer;
  transition: background-color .1s ease-in-out, color .1s ease-in-out;

  &:hover {
    background-color: #000;
    color: #fff;
  }
}